/* Generic CSS */

@import url("https://fonts.googleapis.com/css?family=Nunito:400,900");

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-thumb {
  background-color: #6c757d;
  border-radius: 20px;
}

body {
  margin: 0;
  padding: 0;
  text-rendering: optimizeLegibility;
  background-image: url("../src/assets/imgs/Home\ Page/WebsiteBG.png") !important;
}

a {
  text-decoration: none;
}

.puntoLiveBets {
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background-color: #269b44;
  margin-right: 10px;
}

.DivLiveBets {
  display: flex;
  align-items: center;
}

ul {
  list-style: none;
}

[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

[type="radio"] + img {
  cursor: pointer;
}

[type="radio"] + span {
  cursor: pointer;
}

.c-pointer {
  cursor: pointer;
}

.wrapper-main {
  display: flex;
  justify-content: center;
}

.wrapper-main section {
  width: 95%;
}

.toast-close {
  padding: 0.5rem;
  cursor: pointer;
}

/* NavBar Offcanvas */

.rps-logo {
  width: 12rem;
  margin-left: 1rem;
}

.btn-auth-group {
  border-radius: 5px;
  padding: 5px;
}

.btn-auth {
  display: inline-block;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  font-size: 1rem;
  padding: 0.275rem 0.65rem;
}

.puntoVerde {
  width: 15px;
  margin-right: 10px;
  height: 15px;
}

.play-list {
  width: 100%;
  margin: auto;
}

.profile-picture {
  font-size: 1.125rem;
  text-anchor: middle;
  user-select: none;
}

.profile-picture .image {
  width: 2rem !important;
  height: 2rem !important;
}

.rounded-circle {
  border-radius: 50% !important;
}

.time-in-row {
  font-size: 0.9rem;
}

.menu {
  position: fixed;
  bottom: 25px;
  left: calc(50vw - 250px);
}

.discord-icon {
  color: #7289da;
}

.discord-icon:hover {
  color: rgba(114, 137, 218, 0.8);
}

.twitter-icon {
  color: rgba(29, 161, 242, 1);
}

.twitter-icon:hover {
  color: rgba(29, 161, 242, 0.8);
}

.btn-hover {
  font-size: 22px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 80px;
  text-align: center;
  border: none;
  text-decoration: none;
  background-size: 300% 100%;
  border-radius: 20px;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-hover:hover {
  background-position: 100% 0;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-danger:focus {
  color: #fff !important;
  background-color: #232c31 !important;
  border-color: #b02a37 !important;
  border-width: 2px;
}

a.btn-hover {
  color: white;
  display: flex;
  align-items: center;
}

.btn-hover:focus {
  outline: none;
}

.btn-hover.btn-start {
  background-image: linear-gradient(
    to right,
    #667eea,
    #764ba2,
    #6b8dd6,
    #8e37d7
  );
  box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
}

.btn-hover.btn-wallet {
  background-image: linear-gradient(
    to right,
    #fc6076,
    #ff9a44,
    #ef9d43,
    #e75516
  );
  box-shadow: 0 4px 15px 0 rgba(252, 104, 110, 0.75);
}

.btn-hover.btn-green {
  background-image: linear-gradient(
    to right,
    #25aae1,
    #40e495,
    #30dd8a,
    #2bb673
  );
  box-shadow: 0 4px 15px 0 rgba(49, 196, 190, 0.75);
}

.btn-hover.btn-loading {
  background-image: linear-gradient(
    to right,
    #616161,
    #9e9e9e,
    #757575,
    #616161
  );
  box-shadow: 0 4px 15px 0 rgba(129, 129, 129, 0.55);
  cursor: initial !important;
}

.btn-hover.btn-loading .progress {
  width: 75% !important;
  margin-right: auto;
  margin-left: auto;
  margin-top: 0.7rem;
  height: 8px;
}

.social-icons {
  position: fixed;
  bottom: 25px;
  right: 20px;
}

.version-info {
  position: fixed;
  bottom: 25px;
  left: 10px;
  background-color: #1f252b;
  padding: 5px;
  z-index: 98;
  border-radius: 5px;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.sound-toggle {
  position: fixed;
  bottom: 100px;
  left: 10px;
  padding: 5px;
  z-index: 98;
  -moz-transition: all 0.3s ease-in-out;
  -o-transtion: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  width: 35px;
  height: 35px;
}

.sound-toggle button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  border: none;
  color: white;
  background-color: rgb(81, 88, 94);
}

.sound-toggle button:hover {
  background-color: rgba(81, 88, 94, 0.7);
}

.beat-effect {
  animation: beat 0.4s infinite alternate;
  transform-origin: center;
}

@keyframes beat {
  to {
    transform: scale(1.2);
    position: relative;
    z-index: 5;
  }
}

@media (min-width: 1500px) {
  .wrapper-main section {
    width: 85%;
  }
}

@media (min-width: 991px) {
  .footer-expanded .sound-toggle,
  .footer-expanded .version-info {
    left: 385px;
  }
}

.text-justify {
  text-align: justify;
  text-justify: inter-character;
}

.g-btn-historygames {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  width: 100%;
  top: 72px;
  right: 0;
  position: fixed;
}

.valor-matic {
  font-weight: bold;
  display: flex;
  justify-content: center;
  font-size: 40px;
  color: #f1cf61;
  text-shadow: 1px 4px black, 0px 2px black, 2px 3px black, 1px 4px black;
  text-shadow: 3px 0 0 black, 2px 4px 0 black, 0 2px 0 black, 0 -2px 0 black,
    1px 1px black, -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black;
}

/* Game styles */

.game-gifs-wrapper {
  //row g-0 my-5 justify-content-center mt-4
  gap: 10%;
  margin-top: 5rem;
  justify-content: center;
  display: flex;
  margin-right: 50px;
  margin-left: 50px;
}

@media (max-width: 768px) {
  .game-gifs-wrapper {
    //row g-0 my-5 justify-content-center mt-4
    gap: 15%;
    margin-top: 5rem;
    justify-content: center;
