.image-game {
  width: 150%;
}

.DoubleOrNothing {
  margin-top: 15px;
  font-weight: 700;
  margin-bottom: 15px;
  padding: 20px;
  background-color: #40f26a;
  color: #27273d;
  border: none;
  font-size: 20px;
  border-radius: 20px;
  box-shadow: -3px 0px 29px -8px #40f26a;
}

.playing button {
