.gameAntes {
  position: relative;
  justify-content: center;
  display: flex;
}

.gameAntes p {
  position: absolute;
  top: 20px;
  font-size: 20px;
  font-weight: 600;
}

.labelImage {
  position: relative;
  justify-content: center;
  display: flex;
}

.labelImage p {
  position: absolute;
  top: 20px;
  font-size: 25px;
  font-weight: 600;
}

.game-container {
  max-height: 745px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  align-items: center;

  h1 {
    font-size: 35px;
    text-align: center;
    padding: 20px;
  }

  .imgMatic {
    width: 100px;
    height: 100px;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
    margin-right: 20px;
  }

  label.amount {
    margin: 10px;
  }

  label.amount p {
    color: white;
    font-size: 20px;
    font-weight: 800;
  }

  .MaticGeneral {
    margin-top: 50px;
    margin-bottom: 20px;
    border-radius: 1rem;
    border: 1px solid #3d3657;
    height: 100px;
  }

  .amount p {
    font-weight: 600;
    color: #2e2842;
    cursor: pointer;
    text-align: center;
    margin-bottom: 0px;
  }

  .rps-img {
    width: 90px;
    height: 90px;
    margin: 5px;
    cursor: pointer;
  }

  .rock-img {
    background-image: url("./assets/imgs/Bet_Screen/imageRock.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    &:hover {
      background-image: url("./assets/imgs/Bet_Screen/Rock_GIF.gif");
    }
  }

  .paper-img {
    background-image: url("./assets/imgs/Bet_Screen/imagePaper.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    &:hover {
      background-image: url("./assets/imgs/Bet_Screen/Paper_GIF.gif");
    }
  }

  .scissors-img {
    background-image: url("./assets/imgs/Bet_Screen/imageScissors.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    &:hover {
      background-image: url("./assets/imgs/Bet_Screen/Scissors_GIF.gif");
    }
  }

  input[type="radio"][name="hand"]:checked + .rock-img {
    background-image: url("./assets/imgs/Bet_Screen/Rock_GIF.gif");
    background-color: #42f16b;
    background-size: cover;
    border: 5px solid #42f16b;
    border-radius: 30px;
    box-shadow: -3px 0px 29px -8px #40f26a;
  }

  input[type="radio"][name="hand"]:checked + .paper-img {
    background-image: url("./assets/imgs/Bet_Screen/Paper_GIF.gif");
    background-color: #42f16b;
    background-size: cover;
    border: 5px solid #42f16b;
    border-radius: 30px;
    box-shadow: -3px 0px 29px -8px #40f26a;
  }

  input[type="radio"][name="hand"]:checked + .scissors-img {
    background-image: url("./assets/imgs/Bet_Screen/Scissors_GIF.gif");
    background-color: #42f16b;
    background-size: cover;
    border: 5px solid #42f16b;
    border-radius: 30px;
    box-shadow: -3px 0px 29px -8px #40f26a;
  }

  input[type="radio"][name="amount"]:checked + p {
    background-color: #42f16b;
    color: #2c2640;
  }

  .imageOptions {
    width: 300px;
    border-radius: 2rem;
  }

  .anim-win-lose {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    max-width: 100%;
  }

  .game-selection-hand {
    display: flex;
    justify-content: center;
    margin-top: 3rem;
    gap: 5px;
  }

  .game-playing-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    // width: 80vw;
  }

  @media (max-width: 767px) {
    input[type="radio"][name="amount"]:checked + p {
      background-color: #42f16b;
      padding: 10px;
      border-radius: 10px;
      width: 50px;
      height: 50px;
      color: #2c2640;
    }
  }

  @media (min-width: 768px) {
    .rps-img {
      width: 340px;
      height: 440px;
      margin: 5px;
      cursor: pointer;
    }

    label.amount {
      margin: 10px;
    }

    label.amount p {
      color: #27273d;
      width: 80px;
      padding-top: 17px;
      padding-bottom: 17px;
      font-size: 30px;
      font-weight: 800;
      background-color: #544b77;
      position: relative;
      border-radius: 100%;
    }

    label.amount p::before {
      position: absolute;
      content: "";

      border: 2px solid #2c2640;
      top: 3px;
      left: 3px;
      bottom: 3px;
      right: 3px;
      border-radius: 100%;
    }

    // .game-playing-container {
    //     // width: 60vw;
    // }
  }

  @media (min-width: 992px) {
    max-width: 100%;

    .anim-win-lose {
      max-width: 70%;
      margin: 0 auto;
      margin-top: 2rem;
    }

    // .game-playing-container {
    //     width: 40vw;
    // }
  }

  @media (min-width: 1200px) {
    // .game-playing-container {
    //     width: 30vw;
    // }
  }
}

