/* Main Page */

.responsive-card {
  @media (max-width: 1320px) {
    display: flex;
    gap: 10px;
  }
}

.cards-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  padding-top: 1rem;
}

.game-card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  max-width: calc(50% - 10px);
}

.game-card img {
  z-index: 2;
  width: 100%;
  -moz-transition: all 0.3s ease-in-out 0s;
  -o-transition: all 0.3s ease-in-out 0s;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
  border-radius: 20px;
}

.social-card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  max-width: calc(30% - 10px);
}

.social-card img {
  z-index: 2;
  width: 100%;
  -moz-transition: all 0.3s ease-in-out 0s;
  -o-transition: all 0.3s ease-in-out 0s;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
  border-radius: 5px;
}

.card-bg {
  opacity: 0;
  transform: scale(1.6, 1.2);
  -moz-transition: all 0.3s ease-in-out 0s;
  -o-transition: all 0.3s ease-in-out 0s;
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
  position: absolute;
  left: 25%;
  width: 50%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
}

.game-card img:hover,
.social-card img:hover {
  transform: matrix(1, -0.05, 0.05, 1, 0, 0);
}

.game-card a:hover .card-bg,
.social-card a:hover .card-bg {
  opacity: 1;
  transform: scale(1.5);
}

.card-bg.rps-bg {
  background-size: contain;
  background-position: center center;
}

.card-bg.soon-bg,
.card-bg.nfts-bg,
.card-bg.fair-bg,
.card-bg.twitter-bg,
.card-bg.discord-bg {
  background-size: cover;
  background-position: center center;
}

.rps-bg {
  background: url("./assets/imgs/slots_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

.soon-bg {
  background: url("./assets/imgs/soon_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

.nfts-bg {
  background: url("./assets/imgs/nft_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

.fair-bg {
  background: url("./assets/imgs/fair_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

.twitter-bg {
  background: url("./assets/imgs/twitter_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

.discord-bg {
  background: url("./assets/imgs/discord_hover.png") no-repeat;
  margin-top: 5%;
  top: 1%;
}

