.anim-win-lose::before {
  position: absolute;
  content: "";
  left: 23%;
  border: 2px solid #3d3657;
  width: 54%;
  top: 0%;
  bottom: -5%;
  border-radius: 30px;
}

@media (max-width: 991px) {
  .anim-win-lose::before {
    position: absolute;
    content: "";
    left: 12.5%;
    border: 2px solid #3d3657;
    width: 75%;
    top: 0%;
    bottom: -5%;
    border-radius: 30px;
  }
}

@media (max-width: 500px) {
  .anim-win-lose::before {
    position: absolute;
    content: "";

    border: 2px solid #3d3657;
    width: 90%;
    top: 0%;
    bottom: -5%;
    border-radius: 30px;
  }
}

@media (max-width: 770px) {
  .anim-win-lose::before {
    position: absolute;
    content: "";
    border: 2px solid #3d3657;
    width: 75%;
    top: 0%;
    bottom: -5%;
    border-radius: 30px;
  }
}

@media (max-width: 767px) {
  .anim-win-lose::before {
    position: absolute;
    content: "";
    border: 2px solid #3d3657;
    width: 85%;
    left: 7.5%;
    top: 0%;
    bottom: -5%;
    border-radius: 30px;
  }
}

